import React from 'react';
import { NavLink } from 'react-router-dom';
import img1 from "../src/images/img1.svg";

const Home = () =>{
  return (
    <>
    <section id="header" className="d-flex align-items-centre">
     <div className="container-fluid">
      <div className="row">
        <div className="col-10 mx-auto">  
        <div className="row">
          <div className="col-md-6 pt-6 pt-lg-0 order-lg-1 d-flex justify-content-centre flex-column">
            <h1>
              Grow Your Knowledge with <strong className="brand-name"> VibStart</strong>
            </h1>
            <h2 className="my-3">
              We are the team of talented developers 
            </h2> 
            <div className="mt-3">
              <NavLink  className="btn-get-started" to="/service">
                Get Started
              </NavLink>
            </div>
          </div> 
          <div className="col-lg-6 order-1 order-lg-2 header-img">
            <img src={img1} className="img-fluid animated" alt="home img"/>
          </div>
        </div>
       </div>
      </div>
     </div>
    </section>
    </>
  )
}

export default Home;